.circle{
    align-content: center;
    justify-content: center;
    border-radius: .3rem;
}

.apex_chart{
height: auto;
width: auto;
display: inline-flex;
flex-wrap: wrap;
}

.card-body{
    justify-content: space-between;
    width: auto;
    border: 1px solid #ccc;
    border-radius: 2px;
    display: flex;
    flex: auto;
    margin: 0px;
    cursor: pointer;
    -webkit-box-shadow: -2px 10px 22px -16px rgb(0 0 0 / 75%);
    -moz-box-shadow: -2px 10px 22px -16px rgba(0, 0, 0, .75);
    box-shadow: -2px 10px 22px -16px rgb(0 0 0 / 75%);
} 
.card-body:hover{
    -webkit-box-shadow: 2px 10px 22px -5px rgb(5 5 5 / 75%);
    -moz-box-shadow: 2px 10px 22px -5px rgba(5, 5, 5, 0.75);
    box-shadow: 2px 10px 22px -5px rgb(5 5 5 / 75%);
   
   }
.apexcharts-svg{
    width: 180px;
    height: auto;
}

.leavesdashboard{

    border: .5px solid #ccc;
    background-color: whitesmoke;
}